<template>
    <div class="zxwbtn-margin">
        <button class="zxwnoumenom-button zxwnoumenom-button-margin" @click="goto_edit(noumenon_number)"></button>
        <button class="zxwnoumenom-button zxwnoumenom-button-margin zxwbtn-padding"></button>
    </div>
</template>

<script>
 export default{
     props:['noumenon_number'],
         methods:{
         goto_edit(p){
             let id = this.$route.params.nouId;
             if (p === 1){   //

                 this.$router.push({name:'char_edit',params:{nouId:id}})

             } else if (p === 2){//

                 this.$router.push({name:'lit_edit',nouId:id})

             } else if (p === 3){//

                 this.$router.push({name:'terms_edit',nouId:id})

             } else if (p === 4){//

                 //this.$router.push({name:'char_edit',nouId:id})

             } else if (p === 5){//

                 this.$router.push({name:'off_edit',nouId:id})

             } else if (p === 6){//

                 this.$router.push({name:'ins_edit',nouId:id})

             } else if (p === 7){//

                 this.$router.push({name:'pla_edit',nouId:id})
             }
         }
     }
 }
</script>

<style>
    .zxwbtn-margin{
        float:right;
    }
    .zxwbtn-padding{
        margin-right: 10px;
    }

</style>


