<!---->
<template>
    <div>

        <noumenon_title class="zxw-admin-title" :title="this.title">
            <div slot="children" class="zxwbtn-margin">
                <button slot="children" class="zxwnoumenom-button zxwnoumenom-button-margin"  @click="downback()"></button>
                <button slot="children" class="zxwnoumenom-button zxwnoumenom-button-margin"  @click="open_dialog()"></button>
            </div>
        </noumenon_title>

        <!---->
        <table class="table table-bordered zxw-table-box">
            <thead>
                <tr>
                    <th></th>
                    <th ></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>

            <tbody>
                <tr v-for="(item,index) in user_data"> <!--v-for-->
                    <td>{{item.user_name }}</td>
                    <td>{{item.account}}</td>
                    <td>{{item.time}}</td>
                    <td>
                        <img src="../../assets/img/picture-button/change-icon.png" width="20px" height="20px">
                        <button class="zxw-btn-general" @click="open_motal(index)"></button>
                        <img src="../../assets/img/picture-button/delete-icon.png" width="18px" height="18px">
                        <button class="zxw-btn-general"  @click="delete_users(index)"></button>
                    </td>
                </tr>
            </tbody>
        </table>

        <modal :show_modal="this.show_create" v-on:fireclose="close_dialog()" class="zxw-modal-character">
            <div slot="header" class="zxw-character-header">
                <span></span>
            </div>

            <div slot="body" class="zxw-modal-body">
                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="text" id="text2" class="zxw-input-text" placeholder="8-10" onfocus="this.placeholder=''" @blur="if_account('text2')" v-model="account" v-bind:class="{warnBorder:this.is_active[1]}">
                </div>

                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="text" id="text1" class="zxw-input-text" placeholder="2-10" onfocus="this.placeholder=''" @blur="if_name('text1')"  v-model="user_name" v-bind:class="{warnBorder:this.is_active[0]}" >
                </div>

                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="password" id="text3" class="zxw-input-text" placeholder="6-16"  onfocus="this.placeholder=''" @blur="if_pwd('text3')" v-model="pwd" v-bind:class="{warnBorder:this.is_active[2]}">
                </div>

                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="password" id="text4" class="zxw-input-text" placeholder="" onfocus="this.placeholder=''" @blur="dup_pwd('text3','text4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.is_active[3]}" >
                </div>
            </div>

            <div slot="footer" class="zxw-modal-footer">
                <p  class="warn-tip" v-model="tip">{{tip}}</p>
                <div class="zxw-admin-modal-footerbtn">
                    <button class="zxw-prebtn zxw-modal-btn-margin"  @click="close_dialog()" tabindex="-1"></button>
                    <button class="zxw-nextbtn zxw-modal-btn-margin" @click="add_msg()" :disabled="add_if"></button>
                </div>

            </div>
        </modal>


        <modal :show_modal="this.show_change" v-on:fireclose="close_motal()" class="zxw-modal-character">
            <div slot="header" class="zxw-character-header">
                <span></span>
            </div>

            <div slot="body" class="zxw-modal-body">
                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="text" name="user_name" id="chan1" class="zxw-input-account" v-model="back_account" readonly  tabindex="-1">
                </div>

                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="text"  id="chan2" class="zxw-input-text" v-model="back_username"  @change="if_name('chan2')" v-bind:class="{warnBorder:this.is_active[0]}">
                </div>

                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="password"  id="chan3" class="zxw-input-text" placeholder="6-16"  onfocus="this.placeholder=''" @blur="if_pwd('chan3')" v-model="pwd"  v-bind:class="{warnBorder:this.is_active[2]}" >
                </div>

                <div class="zxw-modal-margin">
                    <label class="zxw-modal-label"></label>
                    <input type="password" id="chan4" class="zxw-input-text" placeholder="" onfocus="this.placeholder=''" @blur="dup_pwd('chan3','chan4')" v-model="confirm_pwd" v-bind:class="{warnBorder:this.is_active[3]}">
                </div>
            </div>

            <div slot="footer" class="zxw-modal-footer">
                <p  class="warn-tip" v-model="tip">{{tip}}</p>
                <div class="zxw-admin-modal-footerbtn">
                    <button class="zxw-prebtn zxw-modal-btn-margin"  @click="close_motal()" tabindex="-1"></button>
                    <button class="zxw-nextbtn zxw-modal-btn-margin" @click="confirm_change()" :disabled="add_if"></button>
                </div>

            </div>
        </modal>

        <!---->
        <paginator :max=this.max_page></paginator>

    </div>
</template>


<script type="text/javascript">
    /*let Mock = require('mockjs');

    //
    Mock.mock('/ancient_books/get_user_list.action?page=1','get',{
        "status|200":200,
        "content|20":[{
            'user_id|1-100':100,
            'name':'@FIRST',
            'account|10000000-1000000000':100000000,
            'time':'@DATETIME("yyyy-MM-dd HH:mm:ss")'
        }],
        'max_page|1-100':100
    });

    Mock.mock('/ancient_books/get_user_list.action?page=2','get',{
        "status|200":200,
        "content|20":[{
            'user_id|1-100':100,
            'name':'@FIRST',
            'account|10000000-1000000000':100000000,
            'time':'@DATETIME("yyyy-MM-dd HH:mm:ss")'
        }],
        'max_page|1-100':100
    });*/

    import paginator from "../../component/paginator.vue";
    import noumenon_title from "../../component/noumenon-title.vue";
    import modal from "../../component/modal.vue";
    import bus from '../../lib/bus';
    export default{
        watch: {
            $route(){
                this.get_users();
            }
        },

        components: {
            paginator,
            noumenon_title,
            modal
        },
        data(){
            return {
                title: '',
                show_create: false,
                show_change: false,

                tip: '',    //
                is_active: [false, false, false, false],    //
                add_if: true,    // / 
                check_n: true,   //
                check_a: false,   //

                user_data: [],
                user_name: '',
                account: '',
                pwd: '',
                confirm_pwd: '',
                back_index: '',
                back_username: '',
                back_account: '',
                get_url: '/ancient_books/get_user_list.action', //
                add_url: '/ancient_books/add_user.action',  //
                modify_url: '/ancient_books/modify_user.action',    //
                delete_url: '/ancient_books/delete_user_by_id.action',  //
                name_url: '/ancient_books/check_name.action',    //
                account_url: '/ancient_books/check_account.action',    //

                page: 1,//
                max_page: 3,//
                router_name: 'admin',

                get_user: {},//
                add_user: {},//
                delete_user: {},//
                modify_user: {},//
                cheif_name: {}, //
                cheif_account: {} //
            }
        },

        created: function () {
            this.get_users();
        },

        methods: {
            // 
            open_dialog(){
                this.show_create = true;
            },

            close_dialog() {
                this.show_create = false;
                this.user_name = "";
                this.account = "";
                this.pwd = "";
                this.confirm_pwd = "";
                this.tip = "";
                this.is_active[0] = false;
                this.is_active[1] = false;
                this.is_active[2] = false;
                this.is_active[3] = false;
            },

            // get 
            success_get(response){
                console.log("success get users ");
                this.max_page = response.body.max_page;
                for (let i = 0; i < response.body.content.length; i++) {
                    this.user_data.push({
                        user_name: response.body.content[i].name,
                        account: response.body.content[i].account,
                        time: response.body.content[i].time,
                        user_id: response.body.content[i].user_id
                    });
                }
            },

            fail_get(response){
                console.log("");
            },

            //
            get_users(){
                this.get_user.value = '?page=' + this.$route.params.pageId;
                let new_url = this.get_url + this.get_user.value;
                this.clean_data();
                this.http_json(new_url, 'get', this.get_user, this.success_get, this.fail_get);
            },

            // post success
            success_add(response){
                console.log("success create!");
                this.clean_data();
                this.get_users();
                this.close_dialog(); //
            },

            fail_add(response){
                console.log("fail create!");
            },

            //post
            create_users(){
                //
                this.add_user.name = this.user_name;
                this.add_user.account = this.account;
                this.add_user.pwd = this.pwd;
                this.http_json(this.add_url, 'post', this.add_user, this.success_add, this.fail_add);
            },


            //
            if_name(tag1){
                document.getElementById(tag1).placeholder = "2-10";
                this.add_if = true;    //
                this.is_active[0] = false;

                let x = document.getElementById(tag1).value;
                let y;
                if (x.length !== 0) {
                    y = this.check_name(x);
                    if (y === true) {
                        this.tip = "";
                        this.is_active[0] = false;
                        this.dup_name(x);
                    } else {
                        this.tip = "2-10";
                        this.add_if = true;
                        this.is_active[0] = true;
                    }
                }
            },

            //
            if_account(tag2){
                document.getElementById(tag2).placeholder = "8-10";
                this.add_if = true;    //
                this.is_active[1] = false;

                let x = document.getElementById(tag2).value;
                let y;
                if (x.length !== 0) {
                    y = this.check_user(x);
                    if (y === true) {
                        this.tip = "";
                        this.is_active[1] = false;
                        this.dup_account();
                    } else {
                        this.tip = "8-10";
                        this.add_if = true;
                        this.is_active[1] = true;
                    }
                }
            },


            //
            if_pwd(tag3){
                document.getElementById(tag3).placeholder = "6-16";
                this.add_if = true;    //
                this.is_active[2] = false;

                let x = document.getElementById(tag3).value;
                let y;
                if (x.length !== 0) {
                    y = this.check_pwd(x);
                    if (y === true) {
                        this.add_if = false;
                        this.tip = "";
                        this.is_active[2] = false;
                    } else {
                        this.tip = "6-16";
                        this.add_if = true;
                        this.is_active[2] = true;
                    }
                }
            },

            //
            dup_pwd(tag1, tag2){
                document.getElementById(tag2).placeholder = "";
                this.add_if = true;
                this.is_active[3] = false;
                let x = document.getElementById(tag1).value;
                let y = document.getElementById(tag2).value;
                if (y.length !== 0) {
                    if (y !== x) {
                        this.tip = "";
                        this.add_if = true;
                        this.is_active[3] = true;
                    } else {
                        this.tip = "";
                        this.add_if = false;
                        this.is_active[3] = false;
                    }
                }
            },

            //
            success_name(response){
                console.log("");
                this.add_if = false;    //
            },

            fail_name(response){
                this.is_active[0] = true;
                this.add_if = true;
                this.tip = "!";
            },

            dup_name(n){
                this.cheif_name.value = "?name=" + n;
                let new_url = this.name_url + this.cheif_name.value;
                this.http_json(new_url, 'get', this.cheif_name, this.success_name, this.fail_name);
            },


            //
            success_account(response){
                console.log("");
                this.add_if = false;
            },

            fail_account(response){
                this.is_active[1] = true;
                this.tip = "!";
                this.add_if = true;
            },

            dup_account(){
                this.cheif_account.value = "?account=" + this.account;
                let new_url = this.account_url + this.cheif_account.value;
                this.http_json(new_url, 'get', this.cheif_account, this.success_account, this.fail_account);
            },

            //
            add_msg(){
                let x = document.getElementById("text1").value;
                let y = document.getElementById("text2").value;
                let z = document.getElementById("text3").value;
                let m = document.getElementById("text4").value;
                if (x === "" || y === "" || z === "" || m === "") {
                    //this.tip = "";
                    this.add_if = true;
                } else {
                    this.if_name('text1');
                    this.if_account('text2');
                    this.if_pwd('text3');
                    this.dup_pwd('text3', 'text4');
                    if (this.add_if === false) {
                        this.create_users();
                    }
                }
            },

            // success
            success_delete(response){
                console.log("success delete");
                this.clean_data();   //
                this.get_users();
            },

            fail_delete(response){
                console.log("fail delete");
            },

            delete_users(index){
                this.delete_user.user_id = this.user_data[index].user_id;
                this.http_json(this.delete_url, 'post', this.delete_user, this.success_delete, this.fail_delete);
            },


            // 
            open_motal(index){
                this.show_change = true;
                this.back_index = index;    //ID
                this.back_username = this.user_data[index].user_name;
                this.back_account = this.user_data[index].account;
            },

            close_motal(){
                this.show_change = false;
                this.back_username = '';
                this.back_account = '';
                this.pwd = '';
                this.confirm_pwd = '';
                this.back_index = '';
                this.tip = '';
                this.is_active[0] = false;
                this.is_active[2] = false;
                this.is_active[3] = false;
            },

            success_modify(response){
                console.log("success modify!");
                this.clean_data();
                this.get_users();
                this.close_motal();
            },

            fail_modify(response){
                console.log("fail modify!");
            },

            //
            modify_users(){
                this.modify_user.name = this.back_username;
                this.modify_user.pwd = this.pwd;
                this.modify_user.user_id = this.user_data[this.back_index].user_id;
                this.http_json(this.modify_url, 'post', this.modify_user, this.success_modify, this.fail_modify);
            },

            //
            confirm_change(){
                let x = document.getElementById("chan2").value;
                let y = document.getElementById("chan3").value;
                let z = document.getElementById("chan4").value;
                if (x === "" || y === "" || z === "") {
                    this.tip = ""
                } else {
                    this.if_name('chan2');
                    this.if_pwd('chan3');
                    this.dup_pwd('chan3', 'chan4');
                    if (this.add_if === false) {
                        this.modify_users();
                    }
                }
            },

            clean_data(){
                this.user_data.splice(0, this.user_data.length);
            },

            downback(){
                this.$http.get('/ancient_books/logout.action').then(function () {
                    localStorage.setItem('user', JSON.stringify("guest"));
                    this.$http.get('/ancient_books/getToken.action').then(function (response) {
                        console.log("token");
                        this.$store.commit("change_token",response.body.token);
                        console.log(this.$store.getters.GetToken + " token");
                    },function () {

                    });
                    this.$router.push({path: '/login'});
                    bus.$emit('change_name','')
                }, function () {

                })
            }
        }
    }

</script>



<style>
    .zxw-admin-title{
        margin:20px 0 20px 330px;
    }

    .zxw-btn-admin{
        float:right;
    }

    thead tr th{
        background-color: #a50000;
        color: gainsboro;
        text-align: center;
    }

    tbody tr:nth-child(even) {
        background-color: #f7e8d6;
    }


    /**/
    .zxw-table-box {
        width:940px;
        table-layout: fixed;    /**/
        text-align: center;
        margin:0 0 0 330px;
    }

    .zxw-btn-general{
        background-color: transparent;
        border: none;
        color:#a50000;
        margin:0 20px 0 0;
    }

    .zxw-modal-body{
        margin:50px 0 0 0;
    }

    .zxw-modal-margin{
        margin:0 0 20px 0;
    }

    .zxw-modal-label{
        margin:0 10px 0 20px;
        text-align: right;
        width:100px;
        font-size: 15px;
    }

    .zxw-input-text{
        width:220px;
        height:28px;
        background-color: transparent;
        border: 2px solid black;
        text-align: center;
    }

    .zxw-admin-modal-footerbtn{
        margin:0 0 0 180px;
    }
    .zxw-modal-btn-margin{
        width:100px;
        height:40px;
        margin:0 5px 0 0;
    }

    .warn-tip{
        color: #a50000;
        font-size: 14px;
        text-align:center;
        margin:10px 0 10px 0;
    }

    .warnBorder {
        border: 2px solid #a50000;
    }

    .zxw-input-account{
        width:220px;
        height:28px;
        text-align: center;
        background: transparent;
        border: none;
    }

    .zxw-admin-btn-length{
        width:140px;
        height:45px;
    }
</style>


